@charset "UTF-8";
@tailwind preflight;
/* Fields */
.field {
  margin-bottom: 1rem;
}

.field.mono input,
.field.mono textarea {
  @apply .font-mono;
}

.invalid-feedback {
  @apply .text-red .text-sm .mt-2 .mb-4;
}

.instructions {
  color: #8f98a3;
}

label {
  @apply .inline-block .mb-1;
}

.field .checkbox {
  @apply .mr-2;
}

select {
  @apply .border .border-grey-light .bg-white;
  height: calc(2.25rem + 2px);
}

/* Inputs */
input[type="text"],
input[type="password"],
input[type="tel"],
input[type="url"],
textarea {
  @apply .border .border-grey .p-2 .rounded .block;
}

input[type="text"]:disabled, input[type="text"][readonly],
input[type="password"]:disabled,
input[type="password"][readonly],
input[type="tel"]:disabled,
input[type="tel"][readonly],
input[type="url"]:disabled,
input[type="url"][readonly],
textarea:disabled,
textarea[readonly] {
  @apply .bg-grey-lightest;
}

input[type="text"].is-invalid,
input[type="password"].is-invalid,
input[type="tel"].is-invalid,
input[type="url"].is-invalid,
textarea.is-invalid {
  @apply .border-red;
}

/* Lightswitches */
.lightswitch {
  @apply .relative .block .select-none;
  width: 46px;
  height: 30px;
}

.lightswitch input {
  @apply .absolute .opacity-0;
}

.lightswitch .slider {
  @apply .absolute .pin .cursor-pointer;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.lightswitch .slider:before {
  @apply .absolute .bg-white;
  content: "";
  height: 26px;
  width: 26px;
  left: 2px;
  bottom: 2px;
  -webkit-transition: .1s;
  transition: .1s;
}

.lightswitch input:checked + .slider {
  background-color: #38c172;
}

.lightswitch input:focus + .slider {
  box-shadow: 0 0 1px #38c172;
}

.lightswitch input:checked + .slider:before {
  -webkit-transform: translateX(16px);
  -ms-transform: translateX(16px);
  transform: translateX(16px);
}

.lightswitch .slider.round {
  border-radius: 34px;
}

.lightswitch .slider.round:before {
  border-radius: 50%;
}

.lightswitch.disabled {
  opacity: .4;
}

.lightswitch.disabled .slider {
  @apply .cursor-default;
}

.app {
  @apply .fixed .pin .flex .flex-col;
  /* Header container */
  /* Content container */
}

.app .header-container {
  @apply .bg-grey-lighter .border-b .border-grey-light .w-full;
}

.app .header-container .header {
  @apply .flex .justify-between .px-6 .py-3 .items-center;
}

.app .header-container .header .header-left {
  @apply .flex .items-center;
}

.app .header-container .header .header-left #sidebar-toggle {
  @apply .mr-4 .text-grey-darker;
}

.app .header-container .header .header-left #sidebar-toggle:hover {
  @apply .text-black;
}

.app .header-container .header .header-brand a {
  @apply .text-lg .text-grey-darker .font-bold;
}

.app .header-container .header .header-brand a:hover {
  @apply .no-underline .text-black;
}

.app .header-container .header .actions-right {
  @apply .ml-4;
}

.app .header-container .header .actions-right ul {
  @apply .list-reset .flex .items-center;
}

.app .header-container .header .actions-right ul li {
  @apply .block .ml-3;
}

.app .header-container .header .actions-right ul li > a {
  @apply .block .text-grey-darker;
}

.app .header-container .header .actions-right ul li > a:hover {
  @apply .cursor-pointer .text-black;
}

.app .content-container {
  @apply .flex-1 .flex;
  /* Sidebar */
  /* Main */
}

.app .content-container #sidebar {
  @apply .hidden;
}

.app .content-container #sidebar .sidenav {
  @apply .py-6;
}

.app .content-container #sidebar .sidenav h5 {
  @apply .relative .text-base .mb-2 .text-grey-darker .px-6;
}

.app .content-container #sidebar .sidenav h5 a {
  @apply .text-grey-darker .block;
}

.app .content-container #sidebar .sidenav h5 a:hover {
  @apply .no-underline;
}

.app .content-container #sidebar .sidenav h5 a.disabled {
  @apply .text-grey;
}

.app .content-container #sidebar .sidenav h5 svg {
  width: 13px;
  margin-right: 5px;
  /*@apply .hidden;*/
}

.app .content-container #sidebar .sidenav h5:not(:first-child) {
  @apply .mt-4;
}

.app .content-container #sidebar .sidenav ul {
  @apply .list-reset;
  /*margin-left: 24px;*/
}

.app .content-container #sidebar .sidenav ul li a {
  @apply .block .text-grey-darker .px-6 .py-2 .no-underline;
  padding-left: 46px;
}

.app .content-container #sidebar .sidenav ul li a:hover {
  @apply .text-black;
}

.app .content-container #sidebar .sidenav ul li a.active {
  @apply .bg-grey-light;
}

.app .content-container #sidebar .sidenav ul li a.disabled {
  @apply .text-grey;
}

.app .content-container .main {
  @apply .flex-1 .overflow-auto .px-8 .py-6;
}

.app .content-container .main .main-content {
  @apply .mx-auto;
  max-width: 1440px;
}

@media (max-width: 767px) {
  .app .content-container #sidebar.showing-sidebar {
    @apply .block .bg-grey-lighter .absolute .pin .z-10;
    top: 61px;
  }
}

@media (min-width: 768px) {
  .app .header-container .header .header-left #sidebar-toggle {
    @apply .hidden;
  }
  .app .content-container #sidebar {
    @apply .w-64 .block .border-r;
  }
}

/* Popover */
.popover {
  width: 200px;
  position: absolute;
  top: 0;
  right: 0;
  padding: 20px;
  background: white;
  box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.3);
  z-index: 20;
}

.popover .popover-arrow {
  width: 50px;
  height: 16px;
  position: absolute;
  top: -16px;
  right: 5px;
  overflow: hidden;
}

.popover .popover-arrow::after {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  background: white;
  transform: translateX(-50%) translateY(-50%) rotate(45deg);
  top: 100%;
  left: 50%;
  box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.3);
}

/* Global menu */
.global-menu {
  @apply .relative;
}

.global-menu .toggle {
  @apply .text-lg .p-2 .rounded-full;
}

.global-menu .popover {
  top: 48px;
  right: -13px;
}

.global-menu .popover .popover-arrow {
  right: 5px;
}

/* User menu */
.user-menu {
  @apply .relative;
}

.user-menu .toggle:hover {
  @apply .cursor-pointer;
}

.user-menu .toggle img {
  @apply .rounded-full;
  width: 36px;
}

.user-menu .popover {
  top: 48px;
  right: -12px;
}

.user-menu .popover .popover-arrow {
  right: 5px;
}

