.el-button {
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #fff;
  border: 1px solid #dcdfe6;
  border-color: #dcdfe6;
  color: #606266;
  -webkit-appearance: none;
  text-align: center;
  box-sizing: border-box;
  outline: none;
  margin: 0;
  transition: .1s;
  font-weight: 500;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  padding: 12px 20px;
  font-size: 14px;
  border-radius: 4px;
}

.el-button + .el-button {
  margin-left: 10px;
}

.el-button.is-round {
  padding: 12px 20px;
}

.el-button:hover, .el-button:focus {
  color: teal;
  border-color: #b3d9d9;
  background-color: #e6f2f2;
}

.el-button:active {
  color: #007373;
  border-color: #007373;
  outline: none;
}

.el-button::-moz-focus-inner {
  border: 0;
}

.el-button [class*="el-icon-"] + span {
  margin-left: 5px;
}

.el-button.is-plain:hover, .el-button.is-plain:focus {
  background: #fff;
  border-color: teal;
  color: teal;
}

.el-button.is-plain:active {
  background: #fff;
  border-color: #007373;
  color: #007373;
  outline: none;
}

.el-button.is-active {
  color: #007373;
  border-color: #007373;
}

