}

.el-menu-item.is-active {
  color: teal;
}

.el-menu-item.is-active i {
  color: inherit;
}

.el-submenu {
  list-style: none;
  margin: 0;
  padding-left: 0;
}

.el-submenu__title {
  height: 56px;
  line-height: 56px;
  font-size: 14px;
  color: #303133;
  padding: 0 20px;
  list-style: none;
  cursor: pointer;
  position: relative;
  transition: border-color .3s, background-color .3s, color .3s;
  box-sizing: border-box;
  white-space: nowrap;
}

.el-submenu__title * {
  vertical-align: middle;
}

.el-submenu__title i {
  color: #909399;
}

.el-submenu__title:hover, .el-submenu__title:focus {
  outline: none;
  background-color: #e6f2f2;
}

.el-submenu__title.is-disabled {
  opacity: 0.25;
  cursor: not-allowed;
  background: none !important;
}

.el-submenu__title:hover {
  background-color: #e6f2f2;
}

.el-submenu .el-menu {
  border: none;
}

.el-submenu .el-menu-item {
  height: 50px;
