.el-table {
  position: relative;
  overflow: hidden;
  box-sizing: border-box;
  flex: 1;
  width: 100%;
  max-width: 100%;
  background-color: #fff;
  font-size: 14px;
  color: #606266;
}

.el-table__empty-block {
  min-height: 60px;
  text-align: center;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.el-table__empty-text {
  width: 50%;
  color: #909399;
}

.el-table__expand-column .cell {
  padding: 0;
  text-align: center;
}

.el-table__expand-icon {
  position: relative;
  cursor: pointer;
  color: #666;
  font-size: 12px;
  transition: transform 0.2s ease-in-out;
  height: 20px;
}

.el-table__expand-icon--expanded {
  transform: rotate(90deg);
}

.el-table__expand-icon > .el-icon {
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -5px;
  margin-top: -5px;
}

.el-table__expanded-cell {
  background-color: #fff;
}

.el-table__expanded-cell[class*=cell] {
  padding: 20px 50px;
}

.el-table__expanded-cell:hover {
  background-color: transparent !important;
}

.el-table--fit {
  border-right: 0;
  border-bottom: 0;
}

.el-table--fit th.gutter, .el-table--fit td.gutter {
  border-right-width: 1px;
}

.el-table--scrollable-x .el-table__body-wrapper {
  overflow-x: auto;
}

.el-table--scrollable-y .el-table__body-wrapper {
  overflow-y: auto;
}

.el-table thead {
  color: #909399;
  font-weight: 500;
}

.el-table thead.is-group th {
  background: #f5f7fa;
}

.el-table th, .el-table td {
  padding: 12px 0;
  min-width: 0;
  box-sizing: border-box;
  text-overflow: ellipsis;
  vertical-align: middle;
  position: relative;
}

.el-table th.is-center, .el-table td.is-center {
  text-align: center;
}

.el-table th.is-left, .el-table td.is-left {
  text-align: left;
}

.el-table th.is-right, .el-table td.is-right {
  text-align: right;
}

.el-table th.gutter, .el-table td.gutter {
  width: 15px;
  border-right-width: 0;
  border-bottom-width: 0;
  padding: 0;
}

.el-table th.is-hidden > *, .el-table td.is-hidden > * {
  visibility: hidden;
}

.el-table--medium th, .el-table--medium td {
  padding: 10px 0;
}

.el-table--small {
  font-size: 12px;
}

.el-table--small th, .el-table--small td {
  padding: 8px 0;
}

.el-table--mini {
  font-size: 12px;
}

.el-table--mini th, .el-table--mini td {
  padding: 6px 0;
}

.el-table tr {
  background-color: #fff;
}

.el-table tr input[type="checkbox"] {
  margin: 0;
}

.el-table th.is-leaf, .el-table td {
  border-bottom: 1px solid #ebeef5;
}

.el-table th.is-sortable {
  cursor: pointer;
}

.el-table th {
  white-space: nowrap;
  overflow: hidden;
  user-select: none;
  text-align: left;
  background-color: #fff;
}

.el-table th div {
  display: inline-block;
  padding-left: 10px;
  padding-right: 10px;
  line-height: 40px;
  box-sizing: border-box;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.el-table th > .cell {
  position: relative;
  word-wrap: normal;
  text-overflow: ellipsis;
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  box-sizing: border-box;
}

.el-table th > .cell.highlight {
  color: teal;
}

.el-table th.required > div::before {
  display: inline-block;
  content: "";
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #ff4d51;
  margin-right: 5px;
  vertical-align: middle;
}

.el-table td div {
  box-sizing: border-box;
}

.el-table td.gutter {
  width: 0;
}

.el-table .cell {
  box-sizing: border-box;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
  word-break: break-all;
  line-height: 23px;
  padding-left: 10px;
  padding-right: 10px;
}

.el-table .cell.el-tooltip {
  white-space: nowrap;
  min-width: 50px;
}

.el-table--group, .el-table--border {
  border: 1px solid #ebeef5;
}

.el-table--group::after, .el-table--border::after, .el-table::before {
  content: '';
  position: absolute;
  background-color: #ebeef5;
  z-index: 1;
}

.el-table--group::after, .el-table--border::after {
  top: 0;
  right: 0;
  width: 1px;
  height: 100%;
}

.el-table::before {
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
}

.el-table--border {
  border-right: none;
  border-bottom: none;
}

.el-table--border.el-loading-parent--relative {
  border-color: transparent;
}

.el-table--border th, .el-table--border td {
  border-right: 1px solid #ebeef5;
}

.el-table--border th:first-child .cell, .el-table--border td:first-child .cell {
  padding-left: 10px;
}

.el-table--border th.gutter:last-of-type {
  border-bottom: 1px solid #ebeef5;
  border-bottom-width: 1px;
}

.el-table--border th {
  border-bottom: 1px solid #ebeef5;
}

.el-table--hidden {
  visibility: hidden;
}

.el-table__fixed, .el-table__fixed-right {
  position: absolute;
  top: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.12);
}

.el-table__fixed::before, .el-table__fixed-right::before {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: #ebeef5;
  z-index: 4;
}

.el-table__fixed-right-patch {
  position: absolute;
  top: -1px;
  right: 0;
  background-color: #fff;
  border-bottom: 1px solid #ebeef5;
}

.el-table__fixed-right {
  top: 0;
  left: auto;
  right: 0;
}

.el-table__fixed-right .el-table__fixed-header-wrapper,
.el-table__fixed-right .el-table__fixed-body-wrapper,
.el-table__fixed-right .el-table__fixed-footer-wrapper {
  left: auto;
  right: 0;
}

.el-table__fixed-header-wrapper {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 3;
}

.el-table__fixed-footer-wrapper {
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 3;
}

.el-table__fixed-footer-wrapper tbody td {
  border-top: 1px solid #ebeef5;
  background-color: #f5f7fa;
  color: #606266;
}

.el-table__fixed-body-wrapper {
  position: absolute;
  left: 0;
  top: 37px;
  overflow: hidden;
  z-index: 3;
}

.el-table__header-wrapper, .el-table__body-wrapper, .el-table__footer-wrapper {
  width: 100%;
}

.el-table__footer-wrapper {
  margin-top: -1px;
}

.el-table__footer-wrapper td {
  border-top: 1px solid #ebeef5;
}

.el-table__header, .el-table__body, .el-table__footer {
  table-layout: fixed;
  border-collapse: separate;
}

.el-table__header-wrapper, .el-table__footer-wrapper {
  overflow: hidden;
}

.el-table__header-wrapper tbody td, .el-table__footer-wrapper tbody td {
  background-color: #f5f7fa;
  color: #606266;
}

.el-table__body-wrapper {
  overflow: hidden;
  position: relative;
}

.el-table__body-wrapper.is-scrolling-none ~ .el-table__fixed,
.el-table__body-wrapper.is-scrolling-none ~ .el-table__fixed-right {
  box-shadow: none;
}

.el-table__body-wrapper.is-scrolling-left ~ .el-table__fixed {
  box-shadow: none;
}

.el-table__body-wrapper.is-scrolling-right ~ .el-table__fixed-right {
  box-shadow: none;
}

.el-table__body-wrapper .el-table--border.is-scrolling-right ~ .el-table__fixed-right {
  border-left: 1px solid #ebeef5;
}

.el-table__body-wrapper .el-table--border.is-scrolling-left ~ .el-table__fixed {
  border-right: 1px solid #ebeef5;
}

.el-table .caret-wrapper {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  height: 34px;
  width: 24px;
  vertical-align: middle;
  cursor: pointer;
  overflow: initial;
  position: relative;
}

.el-table .sort-caret {
  width: 0;
  height: 0;
  border: solid 5px transparent;
  position: absolute;
  left: 7px;
}

.el-table .sort-caret.ascending {
  border-bottom-color: #c0c4cc;
  top: 5px;
}

.el-table .sort-caret.descending {
  border-top-color: #c0c4cc;
  bottom: 7px;
}

.el-table .ascending .sort-caret.ascending {
  border-bottom-color: teal;
}

.el-table .descending .sort-caret.descending {
  border-top-color: teal;
}

.el-table .hidden-columns {
  visibility: hidden;
  position: absolute;
  z-index: -1;
}

.el-table--striped .el-table__body tr.el-table__row--striped td {
  background: #FAFAFA;
}

.el-table--striped .el-table__body tr.el-table__row--striped.current-row td {
  background-color: #e6f2f2;
}

.el-table__body tr.hover-row > td, .el-table__body tr.hover-row.current-row > td, .el-table__body tr.hover-row.el-table__row--striped > td, .el-table__body tr.hover-row.el-table__row--striped.current-row > td {
  background-color: #e6f2f2;
}

.el-table__body tr.current-row > td {
  background-color: #e6f2f2;
}

.el-table__column-resize-proxy {
  position: absolute;
  left: 200px;
  top: 0;
  bottom: 0;
  width: 0;
  border-left: 1px solid #ebeef5;
  z-index: 10;
}

.el-table__column-filter-trigger {
  display: inline-block;
  line-height: 34px;
  cursor: pointer;
}

.el-table__column-filter-trigger i {
  color: #909399;
  font-size: 12px;
  transform: scale(0.75);
}

.el-table--enable-row-transition .el-table__body td {
  transition: background-color .25s ease;
}

.el-table--enable-row-hover .el-table__body tr:hover > td {
  background-color: #f5f7fa;
}

.el-table--fluid-height .el-table__fixed,
.el-table--fluid-height .el-table__fixed-right {
  bottom: 0;
  overflow: hidden;
}

