.el-tag {
  background-color: rgba(0, 128, 128, 0.1);
  display: inline-block;
  padding: 0 10px;
  height: 32px;
  line-height: 30px;
  font-size: 12px;
  color: teal;
  border-radius: 4px;
  box-sizing: border-box;
  border: 1px solid rgba(0, 128, 128, 0.2);
  white-space: nowrap;
}

.el-tag .el-icon-close {
  border-radius: 50%;
  text-align: center;
  position: relative;
  cursor: pointer;
  font-size: 12px;
  height: 16px;
  width: 16px;
  line-height: 16px;
  vertical-align: middle;
  top: -1px;
  right: -5px;
  color: teal;
}

.el-tag .el-icon-close::before {
  display: block;
}

.el-tag .el-icon-close:hover {
