  background: #fff;
  border-radius: 4px;
  line-height: 30px;
  margin: 5px 0;
}

.el-picker-panel__body::after, .el-picker-panel__body-wrapper::after {
  content: "";
  display: table;
  clear: both;
}

.el-picker-panel__content {
  position: relative;
  margin: 15px;
}

.el-picker-panel__footer {
  border-top: 1px solid #e4e4e4;
  padding: 4px;
  text-align: right;
  background-color: #fff;
  position: relative;
  font-size: 0;
}

.el-picker-panel__shortcut {
  display: block;
  width: 100%;
  border: 0;
  background-color: transparent;
  line-height: 28px;
  font-size: 14px;
  color: #606266;
  padding-left: 12px;
