}

.el-range-editor--medium .el-range-separator {
  line-height: 28px;
  font-size: 14px;
}

.el-range-editor--medium .el-range-input {
  font-size: 14px;
}

.el-range-editor--medium .el-range__icon,
.el-range-editor--medium .el-range__close-icon {
  line-height: 28px;
}

.el-range-editor--small.el-input__inner {
