}

.el-range-editor--small .el-range-input {
  font-size: 13px;
}

.el-range-editor--small .el-range__icon,
.el-range-editor--small .el-range__close-icon {
  line-height: 24px;
}

.el-range-editor--mini.el-input__inner {
  height: 28px;
}

.el-range-editor--mini .el-range-separator {
  line-height: 20px;
