  border-radius: 2px;
  position: absolute;
  width: 180px;
  left: 0;
  z-index: 1000;
  user-select: none;
}

.el-time-panel__content {
  font-size: 0;
  position: relative;
  overflow: hidden;
}

.el-time-panel__content::after, .el-time-panel__content::before {
  content: "";
  top: 50%;
  position: absolute;
  margin-top: -15px;
  height: 32px;
  z-index: -1;
  left: 0;
  right: 0;
  box-sizing: border-box;
  padding-top: 6px;
  text-align: left;
  border-top: 1px solid #e4e7ed;
  border-bottom: 1px solid #e4e7ed;
}

.el-time-panel__content::after {
  left: 50%;
  margin-left: 12%;
  margin-right: 12%;
}

.el-time-panel__content::before {
  padding-left: 50%;
  margin-right: 12%;
  margin-left: 12%;
}

.el-time-panel__content.has-seconds::after {
  left: calc(100% / 3 * 2);
}

.el-time-panel__content.has-seconds::before {
  padding-left: calc(100% / 3);
}

