  padding: 10px 15px;
  color: #606266;
  font-size: 14px;
}

.el-message-box__input {
  padding-top: 15px;
}

.el-message-box__input input.invalid {
  border-color: #f56c6c;
}

.el-message-box__input input.invalid:focus {
  border-color: #f56c6c;
}

.el-message-box__status {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
