.el-popper .popper__arrow,
.el-popper .popper__arrow::after {
  position: absolute;
  display: block;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid;
}

.el-popper .popper__arrow {
  border-width: 6px;
  filter: drop-shadow(0 2px 12px rgba(0, 0, 0, 0.03));
}

.el-popper .popper__arrow::after {
  content: " ";
  border-width: 6px;
}

.el-popper[x-placement^="top"] {
  margin-bottom: 12px;
}

.el-popper[x-placement^="top"] .popper__arrow {
  bottom: -6px;
  left: 50%;
  margin-right: 3px;
  border-top-color: #ebeef5;
  border-bottom-width: 0;
}

.el-popper[x-placement^="top"] .popper__arrow::after {
  bottom: 1px;
  margin-left: -6px;
  border-top-color: #fff;
  border-bottom-width: 0;
}

.el-popper[x-placement^="bottom"] {
  margin-top: 12px;
}

.el-popper[x-placement^="bottom"] .popper__arrow {
  top: -6px;
  left: 50%;
  margin-right: 3px;
  border-top-width: 0;
  border-bottom-color: #ebeef5;
}

.el-popper[x-placement^="bottom"] .popper__arrow::after {
  top: 1px;
  margin-left: -6px;
  border-top-width: 0;
  border-bottom-color: #fff;
}

.el-popper[x-placement^="right"] {
  margin-left: 12px;
}

.el-popper[x-placement^="right"] .popper__arrow {
  top: 50%;
  left: -6px;
  margin-bottom: 3px;
  border-right-color: #ebeef5;
  border-left-width: 0;
}

.el-popper[x-placement^="right"] .popper__arrow::after {
  bottom: -6px;
  left: 1px;
  border-right-color: #fff;
  border-left-width: 0;
}

.el-popper[x-placement^="left"] {
  margin-right: 12px;
}

.el-popper[x-placement^="left"] .popper__arrow {
  top: 50%;
  right: -6px;
  margin-bottom: 3px;
  border-right-width: 0;
  border-left-color: #ebeef5;
}

.el-popper[x-placement^="left"] .popper__arrow::after {
  right: 1px;
  bottom: -6px;
  margin-left: -6px;
  border-right-width: 0;
  border-left-color: #fff;
}

.el-cascader {
  display: inline-block;
  position: relative;
  font-size: 14px;
  line-height: 40px;
}

.el-cascader .el-input,
.el-cascader .el-input__inner {
  cursor: pointer;
}

.el-cascader .el-input__icon {
  transition: none;
}

.el-cascader .el-icon-arrow-down {
  transition: transform .3s;
  font-size: 14px;
}

.el-cascader .el-icon-arrow-down.is-reverse {
  transform: rotateZ(180deg);
}

.el-cascader .el-icon-circle-close {
  z-index: 2;
  transition: color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.el-cascader .el-icon-circle-close:hover {
  color: #909399;
}

.el-cascader__clearIcon {
  z-index: 2;
  position: relative;
}

.el-cascader__label {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  padding: 0 25px 0 15px;
  color: #606266;
  width: 100%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  box-sizing: border-box;
  cursor: pointer;
  text-align: left;
  font-size: inherit;
}

.el-cascader__label span {
  color: #000;
}

.el-cascader--medium {
  font-size: 14px;
  line-height: 36px;
}

.el-cascader--small {
  font-size: 13px;
  line-height: 32px;
}

.el-cascader--mini {
  font-size: 12px;
  line-height: 28px;
}

.el-cascader.is-disabled .el-cascader__label {
  z-index: 2;
  color: #c0c4cc;
}

.el-cascader-menus {
  white-space: nowrap;
  background: #fff;
  position: absolute;
  margin: 5px 0;
  z-index: 2;
  border: solid 1px #e4e7ed;
  border-radius: 2px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}

.el-cascader-menu {
  display: inline-block;
  vertical-align: top;
  height: 204px;
  overflow: auto;
  border-right: solid 1px #e4e7ed;
  background-color: #fff;
  box-sizing: border-box;
  margin: 0;
  padding: 6px 0;
  min-width: 160px;
}

.el-cascader-menu:last-child {
  border-right: 0;
}

.el-cascader-menu__item {
  font-size: 14px;
  padding: 8px 20px;
  position: relative;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #606266;
  height: 34px;
  line-height: 1.5;
  box-sizing: border-box;
  cursor: pointer;
  outline: none;
}

.el-cascader-menu__item--extensible:after {
  font-family: 'element-icons';
  content: "\e604";
  font-size: 14px;
  color: #bfcbd9;
  position: absolute;
  right: 15px;
}

.el-cascader-menu__item.is-disabled {
  color: #c0c4cc;
  background-color: #fff;
  cursor: not-allowed;
}

.el-cascader-menu__item.is-disabled:hover {
  background-color: #fff;
}

.el-cascader-menu__item.is-active {
  color: teal;
}

.el-cascader-menu__item:hover, .el-cascader-menu__item:focus:not(:active) {
  background-color: #f5f7fa;
}

.el-cascader-menu__item.selected {
  color: #fff;
  background-color: #f5f7fa;
}

.el-cascader-menu__item__keyword {
  font-weight: bold;
}

.el-cascader-menu--flexible {
  height: auto;
  max-height: 180px;
  overflow: auto;
}

.el-cascader-menu--flexible .el-cascader-menu__item {
  overflow: visible;
}

